import { useEffect, useState } from 'react'
import Sidebar from '../components/Sidebar'
import Header from '../components/Header'
import InvoiceModal, { type InvoiceData } from '../components/InvoiceModal'
import StatusBadge from '../components/StatusBadge'
import { PencilIcon, TrashIcon } from '@heroicons/react/24/outline'

type Invoice = {
  id: number
  title: string
  customer: string
  amount: number
  taxRate: number
  taxAmount: number
  totalAmount: number
  status: string
  dueDate?: string
  description?: string
  createdAt: string
}

const invoiceStatusOptions: Invoice['status'][] = ['open', 'paid', 'overdue']

export default function InvoicesPage() {
  const [invoices, setInvoices] = useState<Invoice[]>([])
  const [loading, setLoading] = useState(true)
  const [isModalOpen, setIsModalOpen] = useState(false)
  const [editingInvoice, setEditingInvoice] = useState<Invoice | null>(null)
  const [refreshKey, setRefreshKey] = useState(0)

  useEffect(() => {
    loadInvoices()
  }, [refreshKey])

  const loadInvoices = async () => {
    setLoading(true)
    try {
      const res = await fetch('/api/invoices', { cache: 'no-store' })
      if (res.ok) {
        const data: Invoice[] = await res.json()
        setInvoices(data)
      }
    } catch (error) {
      console.error('Error loading invoices:', error)
    } finally {
      setLoading(false)
    }
  }

  const handleSaveInvoice = async (data: InvoiceData) => {
    try {
      const method = editingInvoice ? 'PUT' : 'POST'
      const payload = editingInvoice ? { ...data, id: editingInvoice.id } : data
      const response = await fetch('/api/invoices', {
        method,
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(payload)
      })

      if (response.ok) {
        alert(editingInvoice ? 'Rechnung aktualisiert' : `Rechnung "${data.title}" erstellt`)
        setIsModalOpen(false)
        setEditingInvoice(null)
        setRefreshKey(prev => prev + 1)
      } else {
        const err = await response.json()
        alert(err.error || 'Fehler beim Speichern der Rechnung')
      }
    } catch (error) {
      console.error('Error saving invoice:', error)
      alert('Fehler beim Speichern der Rechnung')
    }
  }

  const handleEdit = (invoice: Invoice) => {
    setEditingInvoice(invoice)
    setIsModalOpen(true)
  }

  const handleDelete = async (id: number) => {
    if (!confirm('Rechnung wirklich löschen?')) return
    try {
      const res = await fetch(`/api/invoices?id=${id}`, { method: 'DELETE' })
      if (res.ok) {
        alert('Rechnung gelöscht')
        setRefreshKey(prev => prev + 1)
      } else {
        alert('Fehler beim Löschen der Rechnung')
      }
    } catch (error) {
      console.error('Error deleting invoice:', error)
      alert('Fehler beim Löschen der Rechnung')
    }
  }

  const closeModal = () => {
    setIsModalOpen(false)
    setEditingInvoice(null)
  }

  const handleStatusChange = async (invoice: Invoice, status: Invoice['status']) => {
    if (invoice.status === status) return
    try {
      const payload = {
        id: invoice.id,
        title: invoice.title,
        customer: invoice.customer,
        amount: invoice.amount,
        taxRate: invoice.taxRate,
        dueDate: invoice.dueDate ?? '',
        description: invoice.description ?? '',
        status
      }
      const response = await fetch('/api/invoices', {
        method: 'PUT',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(payload)
      })

      if (response.ok) {
        setRefreshKey(prev => prev + 1)
      } else {
        const err = await response.json()
        alert(err.error || 'Fehler beim Aktualisieren des Status')
      }
    } catch (error) {
      console.error('Error updating status:', error)
      alert('Fehler beim Aktualisieren des Status')
    }
  }

  const modalData: InvoiceData | null = editingInvoice
    ? {
        id: editingInvoice.id,
        title: editingInvoice.title,
        customer: editingInvoice.customer,
        amount: editingInvoice.amount.toString(),
        taxRate: editingInvoice.taxRate.toString(),
        dueDate: editingInvoice.dueDate ?? '',
        description: editingInvoice.description ?? '',
        status: editingInvoice.status as InvoiceData['status'],
      }
    : null

  return (
    <div className="flex min-h-screen bg-gray-50">
      <Sidebar />

      <div className="flex-1 p-8">
        <Header onNewInvoice={() => setIsModalOpen(true)} />

        <div className="mt-6">
          <div className="flex items-center justify-between mb-6">
            <h1 className="text-2xl font-bold">Rechnungen</h1>
            <button
              onClick={() => {
                setEditingInvoice(null)
                setIsModalOpen(true)
              }}
              className="px-4 py-2 bg-blue-600 text-white rounded-md text-sm font-medium hover:bg-blue-700"
            >
              + Neue Rechnung
            </button>
          </div>

          {loading ? (
            <div className="text-center py-8 text-gray-500">Lädt...</div>
          ) : invoices.length === 0 ? (
            <div className="bg-white rounded-lg shadow p-8 text-center">
              <p className="text-gray-500 mb-4">Noch keine Rechnungen vorhanden</p>
              <button
                onClick={() => setIsModalOpen(true)}
                className="px-4 py-2 bg-blue-600 text-white rounded-md text-sm font-medium hover:bg-blue-700"
              >
                Erste Rechnung erstellen
              </button>
            </div>
          ) : (
            <div className="bg-white rounded-lg shadow overflow-hidden">
              <table className="w-full">
                <thead className="bg-gray-50 border-b">
                  <tr>
                    <th className="px-6 py-3 text-left text-sm font-semibold text-gray-700">TITEL</th>
                    <th className="px-6 py-3 text-left text-sm font-semibold text-gray-700">KUNDE</th>
                    <th className="px-6 py-3 text-right text-sm font-semibold text-gray-700">BETRAG</th>
                    <th className="px-6 py-3 text-left text-sm font-semibold text-gray-700">STATUS</th>
                    <th className="px-6 py-3 text-left text-sm font-semibold text-gray-700">FÄLLIG AM</th>
                    <th className="px-6 py-3 text-left text-sm font-semibold text-gray-700">AKTIONEN</th>
                  </tr>
                </thead>
                <tbody>
                  {invoices.map((invoice, idx) => (
                    <tr key={invoice.id} className={idx % 2 === 0 ? 'bg-white' : 'bg-gray-50'}>
                      <td className="px-6 py-4 text-sm font-medium text-gray-900">
                        <div>{invoice.title}</div>
                        <div className="text-xs uppercase tracking-wide text-gray-400 mt-1">Info</div>
                        <p className="text-sm text-gray-500">{invoice.description || 'Keine Beschreibung'}</p>
                      </td>
                      <td className="px-6 py-4 text-sm text-gray-600">{invoice.customer}</td>
                      <td className="px-6 py-4 text-sm font-semibold text-right text-gray-900">€ {invoice.totalAmount.toFixed(2)}</td>
                      <td className="px-6 py-4 text-sm">
                        <StatusBadge status={invoice.status} />
                      </td>
                      <td className="px-6 py-4 text-sm text-gray-600">{invoice.dueDate ? new Date(invoice.dueDate).toLocaleDateString('de-DE') : '–'}</td>
                      <td className="px-6 py-4 text-sm space-x-3">
                        <button
                          onClick={() => handleEdit(invoice)}
                          className="text-blue-600 hover:text-blue-800 inline-flex items-center gap-1"
                        >
                          <PencilIcon className="w-4 h-4" />
                          Bearbeiten
                        </button>
                        <button
                          onClick={() => handleDelete(invoice.id)}
                          className="text-red-600 hover:text-red-800 inline-flex items-center gap-1"
                        >
                          <TrashIcon className="w-4 h-4" />
                          Löschen
                        </button>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          )}
        </div>
      </div>

      <InvoiceModal
        isOpen={isModalOpen}
        onClose={closeModal}
        onSave={handleSaveInvoice}
        initialData={modalData}
      />
    </div>
  )
}
